<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <h4>Add Category</h4>
      <div class="form-group">
        <label for="value">Value<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="value"
          required
          v-model="category.value"
          name="value"
        />
      </div>

      <div class="form-group">
        <label for="text">Text<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="text"
          required
          v-model="category.text"
          name="text"
        />
      </div>
      <div class="row">
        <div class="form-group col-11">
          <label for="image">Image</label>
          <div
            :class="[
              preview_image ? 'custom-file has-image-preview' : 'custom-file',
            ]"
          >
            <input
              type="file"
              accept="image/*"
              class="custom-file-input"
              id="image"
              aria-describedby="inputGroupFileAddon01"
              name="image"
              @change="onFileChangeImage"
            />
            <label
              class="custom-file-label has-image-preview"
              for="image"
              v-if="preview_image"
            >
              <img class="preview" :src="preview_image" />
            </label>
            <label class="custom-file-label" for="image" v-else
              >Choose file</label
            >
          </div>
        </div>
        <div class="remove-images col-1">
          <span
            class="remove-images-input text-danger mt-5 cursor-pointer mr-2"
            type="button"
            v-on:click="removeImage()"
            >X</span
          >
        </div>
      </div>

      <button @click="saveCategory" class="btn btn-success">Submit</button>
      <p>{{ message }}</p>
    </div>

    <div v-else>
      <h4>You submitted successfully!</h4>
      <button class="btn btn-success" @click="newCategory">Add</button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Category from "@/types/Category";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "add-category",
  data() {
    return {
      selected: "Choose Category",
      preview_image: "",
      message: "",
      category: {
        _id: "",
        value: "",
        text: "",
        image: "",
      } as Category,
      submitted: false,
      image: {} as Blob,
    };
  },
  methods: {
    saveCategory() {
      if (!this.category.value || !this.category.text) {
        this.message = "Please input Value and Text.";
        return;
      }
      let data = {
        value: this.category.value,
        text: this.category.text,
      };

      console.log(data);

      DataService.createCategory(data)
        .then((response: ResponseData) => {
          this.category._id = response.data;
          console.log(response.data);
          DataService.uploadImageCategory(
            this.category._id as string,
            this.image
          )
            .then((response: ResponseData) => {
              console.log(response.data);
              this.submitted = true;
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    newCategory() {
      this.submitted = false;
      this.category = {} as Category;
    },

    onFileChangeImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_image = reader.result as string;
          input.files ? (this.image = input.files[0]) : null;
          console.log(input.files);
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    removeImage() {
      this.preview_image = "";
      var imageInput = document.getElementById("image") as HTMLInputElement;
      imageInput.value = "";
    },
  },
});
</script>

<style>
.submit-form {
  max-width: 500px;
  margin: auto auto 40px auto;
}
.has-image-preview {
  height: 150px;
}
.preview {
  height: 100%;
  object-fit: contain;
}
.has-image-preview .custom-file-label::after {
  content: "Change";
  height: 100%;
  display: flex;
  align-items: center;
}
</style>
