
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Category from "@/types/Category";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "add-category",
  data() {
    return {
      selected: "Choose Category",
      preview_image: "",
      message: "",
      category: {
        _id: "",
        value: "",
        text: "",
        image: "",
      } as Category,
      submitted: false,
      image: {} as Blob,
    };
  },
  methods: {
    saveCategory() {
      if (!this.category.value || !this.category.text) {
        this.message = "Please input Value and Text.";
        return;
      }
      let data = {
        value: this.category.value,
        text: this.category.text,
      };

      console.log(data);

      DataService.createCategory(data)
        .then((response: ResponseData) => {
          this.category._id = response.data;
          console.log(response.data);
          DataService.uploadImageCategory(
            this.category._id as string,
            this.image
          )
            .then((response: ResponseData) => {
              console.log(response.data);
              this.submitted = true;
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    newCategory() {
      this.submitted = false;
      this.category = {} as Category;
    },

    onFileChangeImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.preview_image = reader.result as string;
          input.files ? (this.image = input.files[0]) : null;
          console.log(input.files);
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    removeImage() {
      this.preview_image = "";
      var imageInput = document.getElementById("image") as HTMLInputElement;
      imageInput.value = "";
    },
  },
});
